
body{
    background-color:  #333333;
    color:#00ffff;
}

.btn-default,.btn,.form-control{
    border-color: #23ebeb;
    background-color: #525252;
    color:#00ffff;
}

.btn-default:hover,.btn:hover,.form-control:hover{
    background-color: black;
    color:#00ffff;
}

.btn-default:active,.btn-default:active,.form-control:active{
    background-color: #333333 !important;
    color:#00ffff !important;
}

.jumbotron.well-sm{
    background-color: #525252;
}

.navbar{
    background-color: #525252;
}

.navbar-brand{
    color:#00ffff !important;
}

.nav.navbar-nav > li.active > a{
    background-color:#333333 !important;
}

.list-group-item{
    background-color: #525252;
    color:#00ffff !important;
}

.list-group-item:hover{
    background-color: black !important;
    color:#00ffff !important;
}

.list-group-item:active{
    background-color: #333333 !important;
    color:#00ffff !important;
}

.list-group-item.active {
    background-color: #333333;
    color:white !important;
    border-color: white;
}

.friend-online{
    color:lime;
}

.friend-offline{
    color:gray;
}

.panel.panel-info,.panel.panel-danger {
    background-color: #525252;
}

.panel-info > .panel-heading {
    background-color: darkcyan;
}

.panel-danger > .panel-heading {
    background-color:brown;
}

.panel-heading{
    color:white !important;
}

.dropdown-menu{
    background-color: black;
}

.dropdown-menu>li>a {
    color:#00ffff !important;
}

.dropdown-menu>li>a:hover {
    background-color: darkgray;
    color:black !important;
}

.input-messager{
    background-color: #525252;
    color:#00ffff;
}

.chat-messages{
    position: fixed;
    bottom:60px;
    left:30px;
    overflow: auto;
    max-height: 300px;
}

.chat-inputer{
    position: fixed;
    bottom:30px;
    left:20px;
}


.champion:hover {
  outline: 1px solid white;
  outline-offset: -4px;
}

.float{
  position:fixed;
  width:60px;
  height:60px;
  bottom:70px;
  right:40px;
  background-color:#0C9;
  color:#FFF;
  border-radius:50px;
  text-align:center;
  box-shadow: 2px 2px 3px #999;
}

.panel-default{
    background-color: #525252;;
}
